<template>
  <div class="layout">
    <Layout style="height: 100vh;">
      <Header>
        <top1></top1>
      </Header>
      <Layout>
        <Sider hide-trigger :style="{background: '#515a6e',position: 'fixed', height: '100vh', left: 0, overflow: 'auto'}">
          <leftNavi></leftNavi>
        </Sider>
        <Layout :style="{padding: '0 24px 24px',marginLeft: '200px'}">
          <router-view></router-view>
        </Layout>
      </Layout>
    </Layout>
  </div>
</template>

<script>
  import top1 from '../components/top'
  import leftNavi from './leftNavi'
    export default {
        name: "myIframe"
      ,components:{
          top1
        ,leftNavi
      }
    }
</script>

<style scoped>
  .layout{
    /*border: 1px solid #d7dde4;*/
    background: #f5f7f9;
    position: relative;
    border-radius: 4px;
    overflow: hidden;

  }
</style>



