<template>
  <div>
    <Menu mode="horizontal" theme="dark" active-name="1">
      <div class="layout-logo"><router-link to="/">iView Demo</router-link></div>
      <div class="layout-nav">
        <MenuItem name="1">
          <a href="https://www.iviewui.com/components/menu" target="_blank">

            iView 文档说明 <Icon type="md-open" />
          </a>
        </MenuItem>
        <MenuItem name="2">
          <a href="https://cn.vuejs.org/v2/guide/" target="_blank">
            VUE 框架 <Icon type="md-open"></Icon>
          </a>
        </MenuItem>
        <MenuItem name="3">
          <a href="https://router.vuejs.org/zh/" target="_blank">
            Router Vue <Icon type="md-open"></Icon>
          </a>
        </MenuItem>
        <MenuItem name="4">
          <a href="https://vuex.vuejs.org/zh/" target="_blank">
          Vuex <Icon type="md-open"></Icon>
          </a>
        </MenuItem>
        <MenuItem name="5">
          <Button @click="exitBtn" type="dashed">退出系统</Button>

        </MenuItem>
      </div>
    </Menu>
  </div>
</template>

<script>
  export default {
    name: "top"
    ,methods:{
      exitBtn:function(){
        this.$Message.info('退出系统');
        sessionStorage.setItem("isLogin",null)
        this.$router.push("/login")
      }
    }
  }
</script>

<style scoped>
  .layout-logo{
    width: 100px;
    height: 30px;
    background: #5b6270;
    border-radius: 3px;
    float: left;
    position: relative;
    top: 15px;
    left: 20px;


    color: #ffffff;
    line-height: 30px;
    text-align: center;

  }
  .layout-nav{
    width: 820px;
    margin: 0 auto;
    margin-right: 20px;
  }
  /*.layout-nav li { float:right;}*/
  a,a:link,a:hover { color:inherit;}
</style>



